import Vue from 'vue'
import Router from 'vue-router'
import About from '../components/About'
import Faq from '../components/Faq'
import How from '../components/How'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'About',
      component: About,
    },
    {
      path: '/faq',
      name: 'FAQ',
      component: Faq,
    },
    {
      path: '/howtheywork',
      name: 'How They Work',
      component: How,
    },
  ],
})



